* {
  margin: 0;
  padding: 0;

  &::selection {
    background: rgb(34, 255, 175);
    color: red;
  }
  cursor: pointer;

  &:focus{
    outline: none;
  }
}

body {
  ul {
    list-style: none;
  }

  a {
    text-decoration: none;
    color: white;
  }

  button {
    font-family: "Lato", sans-serif;
  }

  font-family: "Lato", sans-serif;
  font-size: 18px;
  background-color: white;
  font-weight: 200;
}

.container {
  max-width: 1420px;
  margin: 0 auto;
  padding: 0 100px;
}

.svg-icon {
  width: 40px;
  height: 30px;
}

.icon_link {
  height: 30px;
  display: block;

  use {
    fill: white;
  }

  &:hover {
    use {
      fill: rgb(0, 220, 170);
    }
  }
}

.header {
  background-color: black;
  position: fixed;
  z-index: 100;
  width: 100%;
}

.button_style {
  width: 300px;
  border: 2px solid rgb(0, 220, 170);
  padding: 15px 60px;
  background-color: transparent;
  color: rgb(0, 220, 170);
  font-size: 30px;

  &:hover {
    background-color: black;
    border: 2px solid rgb(0, 220, 170);
  }
}

.header_head {
  display: flex;
  align-items: center;
  color: white;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 400;
  height: 70px;

  .header_head_menu {
    display: flex;

    ul {
      display: flex;
    }

    ul li {
      margin-right: 40px;

      &:hover {
        font-weight: 400;
        text-decoration: underline;
      }
    }
  }

  .header_head_contacts {
    display: flex;
    align-items: center;

    ul {
      margin-right: 20px;
    }

    ul li {
      font-size: 18px;
    }

    .header_images {
      display: flex;
      align-items: flex-end;
      margin-right: 20px;
    }

    .icon {
      width: 25px;
      height: 25px;
      object-fit: cover;
      padding-right: 7px;

      &:hover {
        color: white;
      }
    }

    .header_head_authorization {
      display: flex;
      flex-direction: column;
      padding: 10px;
      font-size: 20px;

      a:hover {
        color: rgb(34, 255, 175);
        text-decoration: underline;
      }
    }
  }
}

