.slideshow-container {
  width: 100%;
  position: relative;
  margin: auto;

  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }

  @-webkit-keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }

  .mySlides {
    display: none;
  }


  .prev{
    cursor: pointer;
    position: absolute;
    top: 50%;
    padding: 25px;
    color: rgb(116, 143, 139);
    font-weight: bold;
    font-size: 25px;
    transform: translateY(-50%);
    transition: 0.6s ease;
    left: -100px;
    border-radius: 0 3px 3px 0;
    user-select: none;
    &:hover {
      background-color: rgba(116,143,139, 0.8);
      color: rgb(34, 255, 175);
    }
  }

  .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    transform: translateY(-50%);
    padding: 25px;
    color: rgb(116, 143, 139);
    font-weight: bold;
    font-size: 25px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
    &:hover {
      background-color: rgba(116,143,139, 0.8);
      color: rgb(34, 255, 175);
    }
  }

  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
    margin-right: -70px;
  }
}

