.cross {
  height: 30px;
  width: 30px;
  background-color: rgb(0, 14, 30);
  border-radius: 5px;
  position: relative;
  margin-left: 320px;
  margin-bottom: 20px;
  margin-top: -20px;

  &:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: "\2573";
    font-size: 20px;
    color: rgb(244, 112, 94);
    line-height: 30px;
    text-align: center;
    transform: rotate(-90deg);
    transition-duration: 500ms;
  }
  &:hover:after {
    color: rgb(34, 255, 175);
    transform: rotate(90deg);
    transition-duration: 500ms;
  }
}

